<template>
  <div class="questionCard">
    <div class="questionlist">
      <div
        v-for="(item,index) in list"
        :key="index"
        :class="{questionnaireBox:true,questionnaire1:index == 0}"
      >
        <van-swipe-cell :on-close="onClose" :name="index">
          <div class="questionlistPanel">
            <div class="questionlistHeader">
              <div class="questionlistTitle">{{item.PAPER_NAME}}</div>
              <div class="questionlistDate">{{item.QUEST_CODE.substring(0,4)}}年{{item.QUEST_CODE.substring(4,6)}}月{{item.QUEST_CODE.substring(6,8)}}日</div>
            </div>
            <div class="detailBtnPanel">
              <span class="detailBtn" v-if="active == 0" @click="comeDetail(item)">填写问卷</span>
              <span class="detailBtnDone" v-if="active == 1" @click="comeDetail(item)">查看详情</span>
            </div>
          </div>

          <!-- <div class="rightImg" v-if="active == 1">
            <img :src="finishImg" />
          </div>

          <div class="rightImg" v-if="active == 0">
            <img :src="notFinishImg" />
          </div>-->

          <!-- <div class="leftImg" v-if="item.IS_READ == 0"></div> -->

          <template slot="right" v-if="active == 1">
            <van-button square type="danger" text="删除" />
          </template>
        </van-swipe-cell>
      </div>
    </div>
    <div class="bottomSpace" v-if="active == 0"></div>
    <div class="bottomSpace1" v-if="active == 1"></div>
  </div>
</template>

<script>
import { deletePaperData } from "@/services/servers/question";
import QuestionnaireItem from "@/common/servers/questionnaire/QuestionnaireItem.vue";
import { Dialog, Toast } from "vant";

export default {
  props: {
    list: {
      type: Array,
      default: []
    },
    active: {
      type: Number,
      default: 0
    }
  },
  components: {
    QuestionnaireItem
  },
  data() {
    return {
      finishImg: require("@/assets/images/home/question/finish_done.png"),
      notFinishImg: require("@/assets/images/home/question/finish_not.png"),
      questionIcon: require("@/assets/images/home/question/question.png")
    };
  },
  mounted() {},
  watch: {},
  methods: {
    comeDetail(item) {
      console.log(this.active);
      if (this.active == 0) {
        this.$store.dispatch("changeActiveFlag", 0);
        this.$router.push({
          name: "questionnaireNotFinish",
          query: {
            PUBLISH_CODE: item.PUBLISH_CODE,
            QUEST_CODE: item.QUEST_CODE,
            PAPER_CODE: item.PAPER_CODE,
            PUBLISH_TYPE: item.PUBLISH_TYPE
          }
        });
      } else {
        console.log(item);
        if (item.TYPE_FLAG == 1) {
          this.$store.dispatch("changeActiveFlag", 1);
          this.$router.push({
            name: "questionnaireFinish",
            query: {
              PUBLISH_CODE: item.PUBLISH_CODE,
              QUEST_CODE: item.QUEST_CODE,
              PAPER_CODE: item.PAPER_CODE,
              PAPER_NAME: item.PAPER_NAME,
              PUBLISH_TYPE: item.PUBLISH_TYPE
            }
          });
        } else {
          if (item.ALLOW_FLAG == 1) {
            var obj = {
              PUBLISH_CODE: item.PUBLISH_CODE
            };
            this.$router.push({
              name: "totalSurface",
              query: {
                PUBLISH_CODE: item.PUBLISH_CODE
              }
            });
          } else {
            this.$toast("您没有权限查看..");
          }
        }
      }
    },

    onClose(clickPosition, instance, detail) {
      let that = this;
      switch (clickPosition) {
        case "left":
        case "cell":
        case "outside":
          instance.close();
          break;
        case "right":
          Dialog.confirm({
            message: "确定删除吗？"
          }).then(() => {
            instance.close();
            let index = detail.name;
            index = parseInt(index);
            let data = {
              PAPER_CODE: that.list[index].PAPER_CODE,
              PUBLISH_CODE: that.list[index].PUBLISH_CODE,
              USER_CODE: that.$userInfo.OWNER_CODE
            };
            data = { data: JSON.stringify(data) };
            deletePaperData(data).then(
              res => {
                if (res.success == true) {
                  this.onRefreshing();
                }
              },
              err => {}
            );
          });
          break;
      }
    },
    onRefreshing() {
      this.$emit("refreshing");
    }
  }
};
</script>

