<template>
    <div class="questionnaire" title="问卷列表">
       <!-- 头部 -->
       <navigation-bar :isDefault="titleShow" :navBarStyle="headerStyle" :isShowRight="headerRight" @onLeftClick="onLeftClick" >
            <template v-slot:nav-center>
                <search @searchOk="searchOk" :placeholderText='searchTooltip' :searchStyle="searchStyle"></search>
            </template>
       </navigation-bar>
        <!-- 分类列表 -->
       <van-tabs v-model="active" swipeable  animated  color="#17B7BD"  line-height="0.08rem" line-width="0.67rem" @click="clickChange" @change="tabChange">
           
            <van-tab title="未完成">
                <div class="noData" v-if="list.length == 0 && pageFlag == 1" >
                        <div class="lineSpace"></div>
                        <img v-if="searchValue!=''" :src="noSearchDataImg" class="noDataImg">
                        <img v-if="searchValue==''" :src="noDataImg" class="noDataImg">
                        <div v-if="searchValue!=''" class="noDataTxt">暂无相关搜索结果</div>
                        <div v-if="searchValue==''" class="noDataTxt">暂无信息</div>
                        
                         <div class="lineSpace"></div>
                </div>
                <div class="noData" v-if=" pageFlag == 0" >
                        <div class="lineSpace"></div>
                        <img :src="netWork" class="networkImg">
                        <div class="networkTxt">
                             <van-button type="default" @click="reload">重新加载</van-button>
                        </div>
                         <div class="lineSpace"></div>
                </div>

                <van-list
                        v-model="loading"
                        :finished="finished"
                        :offset="30"
                        @load="onLoad"
                        v-if="list.length  > 0"
                        >
                    <question-list :list="list" :active="active"></question-list>
                    </van-list>
                     
            </van-tab>
            <van-tab title="已完成">
                <div class="noData" v-if="list1.length == 0 && pageFlag == 1" >
                        <div class="lineSpace"></div>
                        <img v-if="searchValue!=''" :src="noSearchDataImg" class="noDataImg">
                        <img v-if="searchValue==''" :src="noDataImg" class="noDataImg">
                        <div v-if="searchValue!=''" class="noDataTxt">暂无相关搜索结果</div>
                        <div v-if="searchValue==''" class="noDataTxt">暂无信息</div>
                        <div class="lineSpace"></div>
                </div>
                 <div class="noData" v-if=" pageFlag == 0" >
                        <div class="lineSpace"></div>
                        <img :src="netWork" class="networkImg">
                        <div class="networkTxt">
                           <van-button type="default" @click="reload">重新加载</van-button>
                        </div>
                         <div class="lineSpace"></div>
                </div>

                <van-list
                        v-model="loading"
                        :finished="finished"
                        :offset="30"
                        @load="onLoad"
                        v-if="list1.length  > 0"
                        >
                        <question-list :list="list1" :active="active" @refreshing="refresh"></question-list>
                </van-list>
                     
            </van-tab>
      </van-tabs>
       
    </div>
</template>

<script>
import NavigationBar from '@/common/currency/NavigationBar.vue';
import Search from '@/common/currency/Search.vue';
import QuestionList from '@/page/Services/Questionnaire/QuestionList'
import { getQuestionList,getFinishQuestList,deletePaperData } from '@/services/servers/question';
import QuestionnaireItem from '@/common/servers/questionnaire/QuestionnaireItem.vue';
import { Dialog, Toast } from 'vant';
import config from '@/common/config'

export default {
    name:'questionnaire',
    components:{
        NavigationBar,
        Search,
        QuestionnaireItem,
        QuestionList
    },
    data(){
        return{
            titleShow:false,
            userInfo:{
            },
            pageFlag:3,
            loadFlag:0,
            noData:0,
            noDataImg:require('@/assets/images/default/img_default@2x.png'),
            noSearchDataImg:require('@/assets/images/default/no_searching_img.png'),
            netWork:require('@/assets/images/default/no_network.png'),
            PUBLISH_CODE:'',
            headerRight:false,
            active:0,
            searchValue:'',
            deleteIsShow:false,
            tabList:["未完成","已完成"],
            tabLists:[
                {'title':'未完成',list:[]},
                {'title':"已完成",list:[]}
            ],
            pageTotal:'',
            tabFlag:0,
            imgType:'1',
            loading: false,
            finished: false,
            loading1: false,
            finished1: false,
            listSum:0,
            nowPage:1,
            nowFinishPage:1,
            list:[],
            list1:[],
            firstFlag:0,
            finishImg:require('@/assets/images/home/question/finish_done.png'),
            notFinishImg:require('@/assets/images/home/question/finish_not.png'),
            headerStyle:{
                // 'backgroundColor':'#17B7BD',
            },
            questionIcon:require('@/assets/images/home/question/question.png'),
            searchStyle:{
                // 'backgroundColor':'#17B7BD',
            },
            searchTooltip:"请输入问卷名称..."
        }
    },
    mounted(){
        if(this.$route.query.PUBLISH_CODE){
          this.PUBLISH_CODE = this.$route.query.PUBLISH_CODE || '';
           console.log(this.PUBLISH_CODE)
        }
        this.active = this.activeFlag
        console.log(this.active)
    },
    watch:{
        active:{
           handler(newName, oldName) {
                
                    this.nowPage = 1;
                    this.nowFinishPage = 1;
                    this.list = [];
                    this.list1 = [];
                    this.loading = true;
                    this.finished = false;
                    if(this.active == "0"){
                        this.initData();
                    }else{
                        this.initDataFinish();
                    }
                    
                
    　　    },
    　　    immediate: true
        },
        
    },
    computed:{
        activeFlag(){
            return this.$store.getters.getActiveFlag
        },
      
    },
    methods:{
        searchOk(value) {
            this.nowPage = 1;
            this.nowFinishPage = 1;
            this.searchValue = value
            // this.list = [];
            if(this.active == 0){
                this.list = [];
               
                this.finished = false;
                this.initData();
            }else{
                this.list1 = [];
               
                this.finished = false;
                this.initDataFinish();
            }
           
        },
        reload(){
            this.nowPage = 1;
            this.nowFinishPage = 1;
            if(this.active == 0){
                this.list = [];
                this.finished = false;
                this.initData();
            }else{
                this.list = [];
                this.finished = false;
                this.initDataFinish();
            }
        },
        onLeftClick(){
             this.$store.dispatch("changeActiveFlag", 0);
            this.$router.go(-1);
        },
        onLoad() {
            if(this.active == 0){
                this.initData();
             }else{
                this.initDataFinish();
            }
          
        },
       
        initData(){
            this.loadFlag = 1;
            this.pageFlag = 3
            let data = {
                page:this.nowPage,
                rows:10,
                PAPER_NAME:this.searchValue,
                ROOM_CODE:this.$userInfo.ROOM_CODE,
                USER_CODE:this.$userInfo.OWNER_CODE,
                AREA_CODE:this.$userInfo.AREA_CODE,
                PUBLISH_CODE:this.$route.query.PUBLISH_CODE || '',
                version:config.version,
            }
          
            let that = this;
            data = {data:JSON.stringify(data)}
            getQuestionList(data).then(
                res => {
                    let con = res.questList
                    if(con.length > 0){
                        this.list.push(...con)
                    }
                    this.loading = false;
                    this.nowPage++;
                    that.pageTotal = res.total;
                    if(this.list.length >= res.total){
                        this.finished = true;
                    }
                    this.pageFlag = 1
                },
                err => {
                    console.log(err);
                    this.pageFlag = 0
                    
                }
            )
        },
        initDataFinish(){
            this.loadFlag = 1;
            this.pageFlag = 3
            let data = {
                page:this.nowFinishPage,
                rows:10,
                PAPER_NAME:this.searchValue,
                ROOM_CODE:this.$userInfo.ROOM_CODE,
                USER_CODE:this.$userInfo.OWNER_CODE,
                AREA_CODE:this.$userInfo.AREA_CODE,
                PUBLISH_CODE:this.$route.query.PUBLISH_CODE || '',
                version:config.version,
            }
            let that  = this
           
            data = {data:JSON.stringify(data)}
            getFinishQuestList(data).then(
                 res => {
                    let con = res.questList;
                    if(con.length > 0){
                        this.list1.push(...con)
                    }
                    this.pageFlag = 1
                    this.loading = false;
                    this.nowFinishPage++;
                    that.pageTotal = res.total;
                    if(this.list1.length >= res.total){
                        this.finished = true;
                    }
                },
                err => {
                    this.pageFlag = 0
                    console.log(err);
                }
            )
        },
        tabChange(name,title){
            
        },
        clickChange(name,title){
           
        },
        onClose(clickPosition, instance,detail) {
            let that = this;
            switch (clickPosition) {
                case 'left':
                case 'cell':
                case 'outside':
                instance.close();
                break;
                case 'right':
                Dialog.confirm({
                    message: '确定删除吗？'
                }).then(() => {
                  instance.close()
                   let index = detail.name;
                   index = parseInt(index);
                   let data = {
                       "PAPER_CODE":that.list[index].PAPER_CODE,
                       "PUBLISH_CODE":that.list[index].PUBLISH_CODE,
                       "USER_CODE":that.$userInfo.USER_CODE
                   }
                   data = {data:JSON.stringify(data)}
                   deletePaperData(data).then(
                       res => {
                           if(res.success == true){
                               that.list = [];
                               this.nowPage = 1;
                               if(that.active == 0){
                                  that.initData(1)
                               }else{
                                  that.initDataFinish(1);
                               }
                           }
                       },
                       err => {

                       }
                   )
                });
                break;
        }
        },

        refresh(){
            this.nowPage = 1;
            this.nowFinishPage = 1
            this.list1 = [];
            this.loading = true;
            this.initDataFinish();
        }
    
 
    }
}
</script>

<style>
.questionnaire{
    width: 100%;
    height: 100%;
    background-color: #f9f9f9;
    display: flex;
    flex-direction: column
   
}
.questionnaire .searchBox .center .van-search{
     background-color: #f9f9f9!important;
     padding-right: 8px;
}
.questionnaire .van-tabs{
    flex: 1;
    overflow: hidden;
}
.questionnaire .van-tabs__content {
   overflow-y: auto;
   height: 100%;
}
.questionnaire .van-button--danger {
    height: 100%;
}

.questionnaire .van-cell:not(:last-child)::after {
    border-bottom: 0;
}

.questionnaire .van-tabs__wrap {
    
    background-color: #FFFFFF;
    z-index: 200;
}

.questionnaire .van-cell {
    padding: 0
}
.questionnaire .van-cell:not(:last-child)::after{
    border: none
}
.questionnaire .questionnaireBox{
    width: 94.44%;
    margin: 7px auto;
    background: #FFFFFF;
    box-shadow: 0 0 6px 0 rgba(219,219,219,0.50);
    border-radius: 10px;
    position: relative;
}

.questionnaire .rightImg{
    width: 60px;
    height: 60px;
    position: absolute;
    top: 0;
    right: 0;
}
.questionnaire .rightImg img{
    width: 100%;
    height: 100%;
    display: block;
}
.questionnaire .questionnaire1{
    margin-top: 14px;
}
.questionnaire .van-cell{
    padding: 0;
    background-color: #f9f9f9;
}
.questionnaire .questionnaireHeader{
    width: calc(100% - 12px);
    height: 48px;
    padding-left: 12px;
    display: flex
}
.questionnaire .questionIcon{
    display: block;
    width: 24px;
    height: 24px;
    margin-top: 12px;
}
.questionnaire .questionnaireLeft{
    width: 24px;
}
.questionnaire .questionnaireTitle{
    flex: 1;
    padding: 12px 0 12px 12px;
}

/*  */
.questionnaire .questionnaireCon{
    padding: 12px;
    border-bottom: 1px solid #f8f8f8
}
.questionnaireCon .questionHeader{
    width: 100%;
    height: 20px;
    color: #666;
}
.questionnaireCon .questionCon{
    margin: 12px 0 1px 0;
    height: auto;
    background-color: #f8f8f8;
}
.toDetail{
    height: 48px;
    line-height: 48px;
    width: 100%;
    text-align: center;
    color: #17B7BD;
}
.noData{
    width: 100%;
    height: 100%;
    
}
.noDataImg{
    width: 126px;
    display: block;
    margin:0 auto
}
.networkImg{
    width: 86px;
    display: block;
    margin:0 auto
}
.lineSpace{
    width: 100%;
    height: calc(50% - 150px);
}
.noDataTxt{
    width: 100%;
    text-align: center;
    font-size: 14px;
    line-height: 42px;
    color: #9A9A9A;
}
.networkTxt{
    width: 100%;
    text-align: center;
    font-size: 14px;
    line-height: 42px;
    color: #9A9A9A;
    padding-top: 30px;
}
.questionnaire .van-button--default{
    width: 110px;
    margin: 0 auto;
    border-radius: 10px;
    background-color: #f9f9f9;
}
.questionnaire .van-tab__pane{
    height: 100%;
}

</style>

